import React, { useState } from "react";
import Logo from '../logo.png';

export function LoginForm({
  onLogin,
  loading,
  error,
  footerComponent
}) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  function onSubmit(event) {
    event.preventDefault();
    onLogin({ email, password });
    
  }
  return (
    <div className="introduction">
      <div className="whitebox">
        <h3 className="intro-heading">Brainex</h3>
        <p className="description-intro">Brainex works with BCI technology to provide you with real-time focus and calm levels as well as tools to improve both.</p>
        <p className="analytics">🎯 Focus and Calm Analytics</p>
        <p className="pomodoro">🍅 Pomodoro Timer</p>
        <p className="timer">⏱ Mindfulness Timer</p>
       <img src={Logo} className="logo" alt="logo"/>
       {footerComponent}
    </div>
    <form className="c3 login-form" onSubmit={onSubmit}>
      <h3 className="card-heading">Login</h3>
      {!!error ? <h4 className="card-error">{error}</h4> : null}
      <div className="row2">
        <input
          placeholder="Email Address"
          type="email"
          id="email"
          className="input-boxes2"
          value={email}
          disabled={loading}
          onChange={(e) => setEmail(e.target.value)}
        />
      </div>
      <label className="blank"></label>
      <div className="row">
        <input
          placeholder="Password"
          type="password"
          className="input-boxes"
          value={password}
          disabled={loading}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>
      <div className="row">
        <button type="submit" className="card-btn2" disabled={loading}>
          {loading ? "Logging in..." : "Login"}
        </button>
      </div>
    </form>
    </div>
  );
}

