import React, { useState, useEffect } from "react";
import { navigate } from "@reach/router";

import { notion, useNotion } from "../services/notion";
import { Nav } from "../components/Nav";

export function Calm() {
  const { user } = useNotion();
  const [calm, setCalm] = useState(0);
  function onSubmit(event) {
    event.preventDefault();
    navigate(""); // fix this
  }

  useEffect(() => {
    if (!user) {
      navigate("/login");
    }
  }, [user]);

  useEffect(() => {
    if (!user) {
      return;
    }

    const subscription = notion.calm().subscribe((calm) => {
      const calmScore = Math.trunc(calm.probability * 100);
      setCalm(calmScore);
    });

    return () => {
      subscription.unsubscribe();
    };
  }, [user]);

  function onSubmit3(event) {
    event.preventDefault();
    navigate("/mainmenu");
  }
  function onSubmit4(event) {
    event.preventDefault();
    navigate("/settings");
  }

  return (
    <main className="introduction">
      <div class="whitebox4">
        <svg
          width="28"
          height="28"
          viewBox="0 0 28 28"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          onClick={onSubmit3}
          className="backbutton"
        >
          <path
            className="backpath"
            d="M18.9915 6.53648L17.2825 4.82751L7.72388 14.3861L17.2825 23.9448L18.9915 22.2358L11.1418 14.3861L18.9915 6.53648Z"
            fill="#B5B5B5"
          />
          <circle
            className="backcircle"
            cx="14"
            cy="14"
            r="13.3636"
            stroke="#B5B5B5"
            stroke-width="1.27273"
          />
        </svg>
        <svg
          width="28"
          height="28"
          viewBox="0 0 28 28"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
          className="settings"
          onClick={onSubmit4}
        >
          <circle
            cx="14"
            cy="14"
            r="13.3636"
            stroke="#B5B5B5"
            stroke-width="1.27273"
          />
          <path
            d="M20.7081 14.4071C20.7402 14.1393 20.7617 13.8607 20.7617 13.5714C20.7617 13.2821 20.7402 13.0035 20.6974 12.7357L22.5081 11.3214C22.6688 11.1928 22.7117 10.9571 22.6152 10.775L20.901 7.80713C20.7938 7.61428 20.5688 7.54999 20.376 7.61428L18.2438 8.47142C17.7938 8.12856 17.3224 7.84999 16.7974 7.63571L16.476 5.36428C16.4438 5.15 16.2617 5 16.0474 5H12.6188C12.4045 5 12.2331 5.15 12.201 5.36428L11.8796 7.63571C11.3546 7.84999 10.8724 8.13928 10.4331 8.47142L8.30099 7.61428C8.10813 7.53928 7.88313 7.61428 7.77599 7.80713L6.06171 10.775C5.95457 10.9678 5.99742 11.1928 6.16885 11.3214L7.97956 12.7357C7.9367 13.0035 7.90456 13.2928 7.90456 13.5714C7.90456 13.85 7.92599 14.1393 7.96885 14.4071L6.15814 15.8214C5.99742 15.95 5.95457 16.1857 6.05099 16.3678L7.76528 19.3357C7.87242 19.5285 8.09742 19.5928 8.29027 19.5285L10.4224 18.6714C10.8724 19.0142 11.3438 19.2928 11.8688 19.5071L12.1903 21.7785C12.2331 21.9928 12.4045 22.1428 12.6188 22.1428H16.0474C16.2617 22.1428 16.4438 21.9928 16.4653 21.7785L16.7867 19.5071C17.3117 19.2928 17.7938 19.0035 18.2331 18.6714L20.3652 19.5285C20.5581 19.6035 20.7831 19.5285 20.8902 19.3357L22.6045 16.3678C22.7117 16.175 22.6688 15.95 22.4974 15.8214L20.7081 14.4071V14.4071ZM14.3331 16.7857C12.5653 16.7857 11.1188 15.3393 11.1188 13.5714C11.1188 11.8036 12.5653 10.3571 14.3331 10.3571C16.101 10.3571 17.5474 11.8036 17.5474 13.5714C17.5474 15.3393 16.101 16.7857 14.3331 16.7857Z"
            fill="#B5B5B5"
          />
        </svg>

        <h3 className="calm-heading">Calm Score</h3>
        <p className="description-intro3">
          This score depicts how calm you currently are. All data is live from
          the Neurosity Crown. Coming soon:
        </p>
        <button type="submit" className="timer2" onClick={onSubmit}>
          ⏱ Mindfulness Timer
        </button>
      </div>
      <div className="calm-score"></div>
      <div className="calm-word">{calm}% Calm</div>
    </main>
  );
}
